/* Fullscreen dimmed background */
.loader-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.439); /* dim background */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}

/* Container to hold image and spinner */
.loader-container {
  position: relative;
  width: 120px;
  height: 120px;
  display: flex;
  align-items: center;
  justify-content: center;

}
/* Image stays static */
.loader-image {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  object-fit: contain;
}

/* Spinner around image */
.spinner {
  position: absolute;
  top: 0;
  left: 0;
  width: 120px;
  height: 120px;
  border: 6px solid rgba(255, 255, 255, 0);
  border-top: 6px solid #fff;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

/* Spinner animation */
@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
